// Footer CSS ------- //

.sam-footer-wrapper {
  width: 100%;
  padding-bottom: 3rem;
  background-color: $color-primary-darkest;
  font-family: $font-sans;
  font-size: $base-font-size;
}

.sam-footer-body {
  padding-top: 3rem;
  padding-left: 1.5rem;

  @include media($medium-screen) {
    padding-left: 3rem;
  }

  a {
    text-decoration: none;
  }

  a,
  a:focus,
  a:visited,
  a:hover,
  a:active {
    color: $color-white;

  }

  a:hover {
    text-decoration: underline;
  }
}

.sam-footer-logo {
  a {
    display: inline-block;
    min-height: 10rem;
    min-width: 10rem;
    @include background-image(url('../img/logos/gsa-reverse.svg'), url('../img/logos/gsa-reverse.png'));
    background-size: 10rem auto;
    background-repeat: no-repeat;
  }

  span {
    @include sr-only();
  }
}
